import Vue from 'vue'
import Vuex from 'vuex'
import projsService from '../../services/projs.service.js'
import cloudinaryService from '../../services/cloudinary-service.js'
import userStore from '../modules/userStore.js'


Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        projs: [],
        currProj: '',
        filterBy: {
            name: "",
            category: "all",
            sort: null
        }
    },
    mutations: {
        setProjs(state, { projs }) {
            state.projs = projs
        },
        currProj(state, { proj }) {
            state.currProj = proj
        },
        setFilter(state, { filterBy }) {
            state.filterBy = filterBy
        },
        addProj(state, { proj }) {
            state.projs.unshift(proj)
        },
        updateProj(state, { proj }) {
            const idx = state.projs.findIndex(currProj => currProj._id === proj._id)
            if (idx === -1) return
            state.projs.splice(idx, 1, proj)
        },
        removeProj(state, { id }) {
            const idx = state.projs.findIndex(currProj => currProj._id === id)
            if (idx !== -1) state.projs.splice(idx, 1)
        },
    },
    getters: {
        projsForDisplay(state) {
            return state.projs
        }
    },
    actions: {
        async loadProjs(context) {
            console.log('here');

            // context.commit({ type: 'setInProgress', inProgress: true })
            const projs = await projsService.query(context.state.filterBy)

            context.commit({ type: 'setProjs', projs })
                // context.commit({ type: 'setInProgress', inProgress: false })
            return projs

        },
        async loadProj(context, { projId }) {
            // context.commit({ type: 'setInProgress', inProgress: true })
            const proj = await projsService.getById(projId)
            console.log(proj);
            context.commit({ type: 'currProj', proj })
                // context.commit({ type: 'setInProgress', inProgress: false })
            return proj

        },
        async removeProj(context, payload) {

            const removedProj = await projsService.remove(payload.id)

            context.commit(payload);

        },
        async saveProj(context, { proj }) {
            const isEdit = !!proj._id
            const savedProj = await projsService.save(proj)
            console.log('Saved: ', savedProj);
            context.commit({
                type: (isEdit) ? 'updateProj' : 'addProj',
                proj: savedProj
            })
            return savedProj

        },

        async addImg(context, { imgEv }) {
            const img = await cloudinaryService.uploadImg(imgEv)
            return img


        }
    },
    modules: {
        userStore,
    },

})