<template>
  <div class="proj-filter">
    <input type="text" placeholder="Proj name" v-model="filterBy.name" @input="emitFilter" />
    <toggle-btn v-model="filterBy.inStock" @click.native="emitFilter"></toggle-btn>
    <select v-model="filterBy.category" @change="emitFilter">
      <option value="all">All</option>
      <option value="Adult">Adult</option>
      <option value="Educational">Educational</option>
      <option value="Funny">Funny</option>
    </select>

    <form>
      <input type="radio" name="name" value="name" v-model="filterBy.sort" /> Name
      <br/>
      <input type="radio" name="price" value="price" v-model="filterBy.sort" /> Price
    </form>
  </div>
</template>



<script>
import toggleBtn from "@/components/toggle-btn.vue";

export default {
  name: "projPreview",

  data() {
    return {
      filterBy: {
        name: "",
        category: "all",
        sort: null
      }
    };
  },
  components: {
    toggleBtn
  },
  methods: {
    emitFilter() {
      
      this.$emit("set-filter", JSON.parse(JSON.stringify(this.filterBy)));
    }
  },
  watch: {
    "filterBy.sort"() {
      this.emitFilter();
    }
  }
};
</script>

