import Vue from 'vue'
import VueRouter from 'vue-router'
import projsAppPage from '../views/projsAppPage.vue'
import login from '../views/login.vue'
import projDetails from '../views/proj-details.vue'
import projEdit from '../views/proj-edit.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'projsApp',
        component: projsAppPage
    },
    {
        path: '/login',
        name: 'login',
        component: login,
    },
    {
        path: '/details/:_id?',
        name: 'details',
        component: projDetails
    },
    {
        path: '/edit/:id?',
        name: 'edit',
        component: projEdit
    },

]

const router = new VueRouter({
    routes
})

export default router