import HttpService from './HttpService'

export default {
    query,
    getById,
    remove,
    update,
    save
}

function getById(projId) {
    return HttpService.get(`proj/${projId}`)
}

function remove(projId) {
    return HttpService.delete(`proj/${projId}`)
}

function update(proj) {
    return HttpService.put(`proj/${proj._id}`, proj)
}

function query(filterBy) {
    console.log(filterBy);

    var queryParams = new URLSearchParams(filterBy)
        // var queryStr = `?name=${filterBy.name}&sort=${filterBy.sort}&type=${filterBy.category}&inStock=${filterBy.inStock}`
    return HttpService.get(`proj?${queryParams}`)
}

function save(proj) {
    (proj.createdAt) ? proj.updatedAt = Date.now(): proj.createdAt = Date.now()
    proj.inStock = true
    if (proj._id) {
        return update(proj)
    } else {
        return HttpService.post(`proj`, proj)
    }
}