<template>
  <div class="home">
    <router-link :to="'/edit/'" v-if="loggedinAdmin">Add</router-link>
    <proj-filter @set-filter="setFilter" />
    <projs-list :projs="projsForDisplay" @removeProj="removeProj" />
  </div>
</template>

<script>
// @ is an alias to /src
import projsList from "@/components/projs-list.vue";
import projFilter from "@/components/proj-filter.vue";

export default {
  name: "projApp",
  components: {
    projsList,
    projFilter,
     loggedinAdmin: false
  },

  computed: {
    projsForDisplay() {
      return this.$store.getters.projsForDisplay;
    }
  },
  methods: {
    setFilter(filterBy) {
      this.$store.commit({ type: "setFilter", filterBy });
      this.$store.dispatch({
        type: "loadProjs"
      });
    },
    removeProj(id) {
      this.$store.dispatch({ type: "removeProj", id });
    }
  },

  created() {
    this.$store.dispatch({
      type: "loadProjs"
    });
    if(!this.$store.getters.loggedinUser) return
    (this.$store.getters.loggedinUser.isAdmin === 'true') ? this.loggedinAdmin = true : this.loggedinAdmin = false
  }
};
</script>


