<template>
  <div class="projs-list">
    <ul>
      <li v-for="proj in projs" :key="proj._id">
        <proj-preview :proj="proj" @removeProj="removeProj"></proj-preview>
      </li>
    </ul>
  </div>
</template>



<script>
import projPreview from "@/components/proj-preview.vue";

export default {
  name: "projsList",
  props: {
    projs: Array
  },
  components: {
    projPreview
  },
  methods: {
    removeProj(id) {
      this.$emit("removeProj", id);
    }
  }
};
</script>

