<template>
  <div class="proj-preview" >
    <h5>Name: {{proj.name}} </h5>
    <h5>Price: {{proj.price | localeNum}} </h5>
    <h5>Type: {{proj.type}} </h5>
    <div class = "footer">
    <router-link :to="'/details/' + proj._id">More details</router-link> | 
    <router-link :to="'/edit/' + proj._id" v-if="loggedinAdmin">Edit</router-link> |
    <button @click="removeProj(proj._id)" class="proj-preview-btn" v-if="loggedinAdmin">Delete</button>
    </div>

    </div>
</template>



<script>


export default {
  name: 'projPreview',
  props: {
   proj: Object
  },
  data(){
    return{
      loggedinAdmin: false
    }
  },

  methods:{
    removeProj(id){
      this.$emit('removeProj',id)
    }
  },

  created(){
    if(!this.$store.getters.loggedinUser) return
    (this.$store.getters.loggedinUser.isAdmin === 'true') ? this.loggedinAdmin = true : this.loggedinAdmin = false
  
  },

}
</script>

