<template>
  <div class="proj-details" v-if="proj">
      <h1> {{proj.name}} Details</h1>
     <ul>
         <li>Id: {{proj._id}}</li>
         <li>Price: {{proj.price}}</li>
         <li>Category: {{proj.type}}</li>
         <li>Created at: {{proj.createdAt | date}}</li>
         <li>In stock: {{proj.inStock}}</li>
     </ul>
     <img v-bind:src=proj.imgUrl v-if="proj.imgUrl" class="details-img"/>
  </div>
</template>

<script>
// @ is an alias to /src
import projsList from '@/components/projs-list.vue'

export default {
  name: 'projDetails',
      data() {
        return {
            proj: null,
        }
    },

  async  created() {
        const projId = this.$route.params._id;
        if (projId) {
         const proj =  await this.$store.dispatch({
                    type: 'loadProj',
                    projId
                })
                    this.proj = JSON.parse(JSON.stringify(proj))
                
        }
    },

 
    

}
</script>


<style scoped>
.details-img{
    width: 25%;
}
</style>