<template>
  <div class="proj-edit pages-height">
    <h1>{{headline}}</h1>
    <form @submit.prevent="save">
      <input type="text" placeholder="Proj name" v-model="editedProj.name" />
      <input type="number" placeholder="Proj price" v-model.number="editedProj.price" />
      <select v-model="editedProj.type">
        <option value="Adult">Adult</option>
        <option value="Educational">Educational</option>
        <option value="Funny">Funny</option>
      </select>
      <br>
       <label> Add img!
        <input @change="uploadImg" type="file">
    </label>
    <br>
      <button>Save</button>
    </form>
         <img v-bind:src=editedProj.imgUrl v-if="editedProj.imgUrl" class="details-img"/>

  </div>
</template>



<script>
export default {
  name: "projEdit",
  data() {
    return {
      editedProj: {
        name: "",
        price: 0,
        type:null
      }
    };
  },
  created() {
    const projId = this.$route.params.id;
    if (projId) {
      this.$store
        .dispatch({
          type: "loadProj",
          projId
        })
        .then(proj => (this.editedProj = JSON.parse(JSON.stringify(proj))));
    }
  },
  methods: {
   async save() {
    
    var savedProj = await  this.$store
        .dispatch({
          type: "saveProj",
          proj:JSON.parse(JSON.stringify(this.editedProj))
        })
          this.$router.push("/"); 
    },

    async uploadImg(ev){
      console.log(ev.target.files[0]);
      
      var img = await this.$store.dispatch({
        type:'addImg',
        imgEv:ev
      })
        this.editedProj.imgUrl = img.url      
    }
  },
  computed: {
    headline() {
      return this.editedProj._id ? "Edit" : "Add";
    }
  }
};
</script>

<style scoped>
.details-img{
    width: 25%;
}
</style>
