<template>
  <div class="chatApp" >
      <div @click="openPopUp" >Lets chat</div>
      <pop-up v-if="isClicked" @closePopUp="closePopUp"  >
            <header> <button @click="closePopUp" class="chat-app-btn"> X </button>chat now!</header>
            <ul v-if="msgs" ref="chat">
                <li v-for="msg in msgs" >{{msg}}</li>
            </ul>
            <div >
            <main class="input-papa"><input type="text" placeholder="Lets chat" v-model="userMsg" @keyup.enter="startChat"/></main>
            <footer>esc to esc</footer>
            </div>
      </pop-up>

    </div>
</template>



<script>

import popUp from "@/components/pop-up.vue";


export default {
  name: 'chatApp',
  components:{
      popUp
  },
  data(){
      return{
          isClicked: false,
          userMsg: '',
          msgs: [],
      }
  },
  methods:{
      openPopUp(){
          this.isClicked = true
      },
      closePopUp(){
            this.isClicked = false
            this.msgs = []
      },
      startChat(){
          this.msgs.push('You: ' +this.userMsg )
          setTimeout(() =>(this.msgs.push( 'Support : Sure thing honey')), 1500)
          this.userMsg = ''
        //   var lastChild = this.$refs.chat.lastElementChild
       if (!this.$refs.chat.lastElementChild) {
           return
       }else{
           
          setTimeout(() =>( this.$refs.chat.lastElementChild.scrollIntoView()), 1600)

            // this.$refs.chat.lastElementChild.scrollIntoView()
       }
          
      },
     
  },
}
</script>

<style scoped>
.chat-app-btn{
    width: fit-content;
    background: none;
    border: none;
    float: left;
    padding: 10px;
}

</style>


