

 <template>
 <div class ="gender">
    <div @click="picked('man')">Man</div>
    <div @click="picked('woman')">Woman</div>
    <div @click="picked('unknown')">None of your buisness</div>
    </div>
</template>



<script>

export default {
  name: 'genderPicker',
 props: ['value'],
  data(){
            return { gender: this.value }
        },
        methods: {
            picked(val) {    
                this.gender = val
                this.$emit('input', this.gender)
            }
        },
 
}
</script>


